import React, { useMemo, useRef, useState } from 'react'
import './JS2Native.css'

function FileSelector() {
  const fileInputRef = useRef(null)

  /**  */
  const [acceptImage, setAcceptImage] = useState(true)
  const [acceptVideo, setAcceptVideo] = useState(false)
  const [allowMultiple, setAllowMultiple] = useState(true)

  /**  */
  const [selectedFiles, setSelectedFiles] = useState([])

  /**  accept */
  const acceptType = useMemo(() => {
    const types = []
    if (acceptImage) types.push('image/*')
    if (acceptVideo) types.push('video/*')
    return types.length > 0 ? types.join(',') : undefined
  }, [acceptImage, acceptVideo])

  /**  */
  const onFileChooseClick = () => {
    fileInputRef.current?.click()
  }

  /**  */
  const handleFileChange = (e) => {
    const files = Array.from(e.target.files || [])
    setSelectedFiles(files)
  }

  /**  */
  const formatFileSize = (bytes) => {
    if (!bytes) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return `${(bytes / Math.pow(k, i)).toFixed(2)} ${sizes[i]}`
  }

  /**  */
  const imageFiles = useMemo(
    () => selectedFiles.filter(file => file.type.startsWith('image/')),
    [selectedFiles]
  )

  return (
    <div style={{ textAlign: 'center' }}>
      {/*  file input */}
      <input
        ref={fileInputRef}
        type="file"
        style={{ display: 'none' }}
        accept={acceptType}
        multiple={allowMultiple}
        onChange={handleFileChange}
      />

      {/*  +  */}
      <div style={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        gap: 16
      }}>
        <button
          type="button"
          style={{ fontSize: 30 }}
          onClick={onFileChooseClick}
        >
          Select Files
        </button>

        <label style={{ fontSize: 20 }}>
          <input
            type="checkbox"
            checked={acceptImage}
            onChange={(e) => setAcceptImage(e.target.checked)}
          />
          
        </label>

        <label style={{ fontSize: 20 }}>
          <input
            type="checkbox"
            checked={acceptVideo}
            onChange={(e) => setAcceptVideo(e.target.checked)}
          />
          
        </label>

        <label style={{ fontSize: 20 }}>
          <input
            type="checkbox"
            checked={allowMultiple}
            onChange={(e) => setAllowMultiple(e.target.checked)}
          />
          
        </label>
      </div>

      {/*  */}
      <div style={{ marginTop: 12 }}>
        {selectedFiles.length > 0 ? (
          <>
            <p>Selected {selectedFiles.length} file(s):</p>
            <ul>
              {selectedFiles.map(file => (
                <li key={file.name + file.size}>
                  {file.name}{formatFileSize(file.size)}
                </li>
              ))}
            </ul>
          </>
        ) : (
          <p>No files selected</p>
        )}
      </div>

      {/*  */}
      <div className="preview-container">
        {imageFiles.map(file => {
          const url = URL.createObjectURL(file)
          return (
            <div className="preview-card" key={file.name + file.size}>
              <img
                className="preview-image"
                src={url}
                alt={file.name}
                onLoad={() => URL.revokeObjectURL(url)}
              />

              <div className="preview-info">
                <div className="preview-name">{file.name}</div>
                <div className="preview-details">
                  <div>: {formatFileSize(file.size)}</div>
                </div>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default FileSelector
